/* eslint-disable security/detect-object-injection */
import https from 'node:https';
import * as querystring from 'node:querystring';

import { ParsedMail, simpleParser } from 'mailparser';

import {
  EMAIL_SELECTABLE_FIELD,
  Office365Folder,
  Office365FolderList,
  Office365Mail,
  Office365MailAttachment,
  Office365MailAttachmentList,
  Office365MailCategories,
  Office365MailList,
  WELL_KNOWN_EMAIL_BOXES,
} from './office365-types';

export class Office365Client {
  /**
   * The base URL in the MS Graph API for the user.
   *
   * @example https://graph.microsoft.com/v1.0/users/{user}
   */
  readonly #base: string;

  readonly #emlTestMode: boolean = false;

  #token: string | undefined;

  #folderCache: { [key: string]: Office365Folder } = {};

  #mailCache: { [key: string]: Office365Mail } = {};

  constructor(userPrincipalName: string, emlTestMode = false) {
    this.#base = `https://graph.microsoft.com/v1.0/users/${encodeURIComponent(
      userPrincipalName,
    )}`;
    this.#emlTestMode = emlTestMode;
  }

  async init(
    tenantId: string,
    clientId: string,
    clientSecret: string,
  ): Promise<void> {
    this.#folderCache = {};
    this.#mailCache = {};
    this.#token = await this.#getAccessToken(tenantId, clientId, clientSecret);
  }

  async getFolder(
    displayName: string,
    topMailboxes = 100,
  ): Promise<Office365Folder | null> {
    const loweredDisplayName = displayName.toLowerCase();

    if (this.#folderCache[loweredDisplayName]) {
      return this.#folderCache[loweredDisplayName];
    }

    // Try well-known names directly: /mailFolders/{displayName}
    if (WELL_KNOWN_EMAIL_BOXES.includes(loweredDisplayName)) {
      const data = await this.#graphRequest<Office365Folder>(
        `${this.#base}/mailFolders/${encodeURIComponent(displayName)}`,
      );

      if (data?.id) {
        this.#folderCache[loweredDisplayName] = data;
        return data;
      }
    }

    // Otherwise list top folders and match by displayName
    const data = await this.#graphRequest<Office365FolderList>(
      `${this.#base}/mailFolders?$top=${topMailboxes}`,
    );

    const folder =
      data?.value?.find(
        (folder) => folder.displayName?.toLowerCase() === loweredDisplayName,
      ) ?? null;

    this.#folderCache[loweredDisplayName] = folder;

    return folder;
  }

  async getMail(messageId: string): Promise<Office365Mail | null> {
    if (this.#mailCache[messageId]) {
      return this.#mailCache[messageId];
    }

    const params = new URLSearchParams();
    params.set('$filter', `internetMessageId eq '${messageId}'`);

    const searchResult = await this.#graphRequest<Office365MailList>(
      `${this.#base}/messages?${params.toString()}`,
    );

    const mail = searchResult.value?.[0] ?? null;
    this.#mailCache[messageId] = mail;

    return mail;
  }

  async getMails(
    folder: Office365Folder,
    limit = 100,
    fromEpochMs = 0,
  ): Promise<Array<Office365Mail>> {
    // Use lastModifiedDateTime to capture moves/edits
    const iso = new Date(fromEpochMs).toISOString();

    const params = new URLSearchParams();
    params.set('$select', EMAIL_SELECTABLE_FIELD.join(','));
    params.set('$orderby', 'lastModifiedDateTime asc');
    params.set('$filter', `lastModifiedDateTime ge ${iso}`);
    params.set('$top', limit.toString());

    const out: Array<Office365Mail> = [];

    let url = `${this.#base}/mailFolders/${folder.id}/messages?${params.toString()}`;
    while (url && url !== '') {
      // eslint-disable-next-line no-await-in-loop
      const page = await this.#graphRequest<Office365MailList>(url);

      if (page?.value?.length) {
        out.push(...page.value);
      }

      url = page?.['@odata.nextLink'] || null;
    }

    return out;
  }

  async getFullMail(
    folder: Office365Folder,
    mail: Office365Mail,
  ): Promise<ParsedMail> {
    // always build a ParsedMail first (IMAP-compatible processing)
    let mimeBuf: Buffer | null = null;

    if (this.#emlTestMode !== false) {
      // test mode: prefer attached .eml if present
      mimeBuf = await this.#tryGetEmlBuffer(folder.id, mail.id);
    }

    if (!mimeBuf) {
      // fallback (and production): raw MIME of the message itself
      mimeBuf = await this.#downloadMessageMime(folder.id, mail.id);
    }

    return simpleParser(mimeBuf);
  }

  async getMailCategories(
    mail: Office365Mail,
  ): Promise<Office365MailCategories> {
    return this.#graphRequest<Office365MailCategories | undefined>(
      `${this.#base}/messages/${encodeURIComponent(
        mail.id,
      )}?$select=categories`,
      'GET',
      undefined,
      {
        Prefer: 'outlook.body-content-type="text"',
      },
    );
  }

  async updateMailCategories(
    mail: Office365Mail,
    categories: Office365MailCategories,
  ): Promise<void> {
    await this.#graphRequest(
      `${this.#base}/messages/${encodeURIComponent(mail.id)}`,
      'PATCH',
      categories,
    );
  }

  async createReply(
    mail: Office365Mail,
    body?: string,
    from?: string,
  ): Promise<Office365Mail> {
    const draft = await this.#graphRequest<Office365Mail>(
      `${this.#base}/messages/${encodeURIComponent(mail.id)}/createReply`,
      'POST',
    );

    if (!body && !from) {
      return draft;
    }

    await this.#graphRequest(
      `${this.#base}/messages/${encodeURIComponent(draft.id)}`,
      'PATCH',
      {
        body: body
          ? {
              contentType: 'html',
              content: body,
            }
          : undefined,
        from: from ? { emailAddress: { address: from } } : undefined,
      },
    );

    return draft;
  }

  sendMail(mail: Office365Mail): Promise<void> {
    return this.#graphRequest(
      `${this.#base}/messages/${encodeURIComponent(mail.id)}/send`,
      'POST',
    );
  }

  moveMailToFolder(
    mail: Office365Mail,
    folder: Office365Folder,
  ): Promise<void> {
    return this.#graphRequest(
      `${this.#base}/messages/${encodeURIComponent(mail.id)}/move`,
      'POST',
      {
        destinationId: folder.id,
      },
    );
  }

  async listAttachments(
    folderId: string,
    messageId: string,
    limit = 50,
  ): Promise<Array<Office365MailAttachment>> {
    const data = await this.#graphRequest<Office365MailAttachmentList>(
      `${this.#base}/mailFolders/${folderId}/messages/${messageId}/attachments?$expand=microsoft.graph.itemAttachment/item&$top=${limit}`,
      'GET',
    );

    return data?.value || [];
  }

  #getAccessToken(
    tenantId: string,
    clientId: string,
    clientSecret: string,
  ): Promise<string> {
    return new Promise((resolve, reject) => {
      const postData = querystring.stringify({
        client_id: clientId,
        scope: 'https://graph.microsoft.com/.default',
        client_secret: clientSecret,
        grant_type: 'client_credentials',
      });

      const options = {
        hostname: 'login.microsoftonline.com',
        path: `/${tenantId}/oauth2/v2.0/token`,
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
          'Content-Length': Buffer.byteLength(postData),
        },
      };

      const req = https.request(options, (res) => {
        let data = '';

        res.on('data', (chunk) => {
          data += chunk;
        });

        res.on('end', () => {
          try {
            const parsed = JSON.parse(data);
            resolve(parsed.access_token);
          } catch (err) {
            reject(err);
          }
        });
      });

      req.on('error', (e) => {
        reject(e);
      });
      req.write(postData);
      req.end();
    });
  }

  async #graphRequest<T = unknown>(
    urlStr: string,
    method: 'GET' | 'POST' | 'PATCH' = 'GET',
    body: unknown | undefined = undefined,
    extraHeaders: Record<string, string> | undefined = undefined,
  ): Promise<T | undefined> {
    if (!this.#token) {
      throw new Error('Graph token missing (call init())');
    }

    const url = new URL(urlStr);
    const payload =
      body === undefined
        ? undefined
        : Buffer.from(JSON.stringify(body), 'utf-8');

    const options: https.RequestOptions = {
      method,
      hostname: url.hostname,
      path: url.pathname + url.search,
      protocol: url.protocol,
      port: url.port || 443,
      headers: {
        Authorization: `Bearer ${this.#token}`,
        'Content-Type': 'application/json',
        Accept: 'application/json',
        ...(payload ? { 'Content-Length': String(payload.length) } : {}),
        ...(extraHeaders || {}),
      },
    };

    return new Promise<T | undefined>((resolve, reject) => {
      const req = https.request(options, (res) => {
        const chunks: Array<Buffer> = [];
        res.on('data', (d) => chunks.push(Buffer.from(d)));
        res.on('end', () => {
          const status = res.statusCode || 0;
          const raw = Buffer.concat(
            chunks as unknown as ReadonlyArray<Uint8Array>,
          ).toString('utf-8');

          if (status >= 200 && status < 300) {
            if (!raw) return resolve(undefined);
            const ct = (res.headers['content-type'] || '')
              .toString()
              .split(';')[0]
              .trim();
            if (ct === 'application/json') {
              try {
                return resolve(JSON.parse(raw));
              } catch {
                return resolve(undefined);
              }
            } else {
              // non-JSON success (rare in Graph), just return as-is
              // eslint-disable-next-line @typescript-eslint/no-explicit-any
              return resolve(raw as any);
            }
          } else if (status === 204) {
            return resolve(undefined);
          } else {
            return reject(
              new Error(
                `Graph ${method} ${url.pathname}${url.search} failed: ${status} ${raw}`,
              ),
            );
          }
        });
      });
      req.on('error', reject);
      if (payload) req.write(payload);
      req.end();
    });
  }

  async #downloadMessageMime(
    folderId: string,
    messageId: string,
  ): Promise<Buffer> {
    return this.#graphRequest<Buffer>(
      `${this.#base}/mailFolders/${folderId}/messages/${messageId}/$value`,
    );
  }

  async #tryGetEmlBuffer(
    folderId: string,
    messageId: string,
  ): Promise<Buffer | null> {
    const attachments = await this.listAttachments(folderId, messageId);

    // 1) fileAttachment with contentType message/rfc822 or name ends with .eml
    const fileEml = attachments.find(
      (attachment) =>
        attachment['@odata.type'] === '#microsoft.graph.fileAttachment' &&
        ((attachment.contentType || '').toLowerCase() === 'message/rfc822' ||
          String(attachment.name || attachment.fileName || '')
            .toLowerCase()
            .endsWith('.eml')),
    );

    if (fileEml?.contentBytes) {
      // @ts-expect-error contentBytes is base64-encoded string
      return Buffer.from(fileEml.contentBytes, 'base64');
    }

    if (fileEml?.id) {
      // Download via $value just in case contentBytes was trimmed
      return this.#graphRequest<Buffer>(
        `${this.#base}/mailFolders/${folderId}/messages/${messageId}/attachments/${encodeURIComponent(fileEml.id)}/$value`,
      );
    }

    // 2) itemAttachment (embedded message)
    const item = attachments.find(
      (a) =>
        a['@odata.type'] === '#microsoft.graph.itemAttachment' &&
        (a.item?.['@odata.type'] === '#microsoft.graph.message' ||
          a.contentType === 'message/rfc822'),
    );

    if (item?.id) {
      return this.#graphRequest<Buffer>(
        `${this.#base}/mailFolders/${folderId}/messages/${messageId}/attachments/${encodeURIComponent(item.id)}/$value`,
      ); // raw MIME stream
    }

    return null;
  }
}
